import { Component, OnInit } from '@angular/core';
import { SessionService } from 'app/shared/services/session.service';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';
import { DialogService } from 'app/shared/services/dialog.service';
import { ReportService } from '../report.service';
import { PageBaseComponent } from 'app/shared/components/base/page-base.component';
import { HttpErrorResponse } from '@angular/common/http';
import { ILoginSuccess } from 'app/authentication/login/models/i-login-success';
import { ActivatedRoute, Router } from '@angular/router';
import { FuseSplashScreenService } from '@fuse/services/splash-screen.service';

@Component({
  selector: 'app-blocked-ip-list',
  templateUrl: './blocked-ip-list.component.html',
  styleUrls: ['./blocked-ip-list.component.scss']
})
export class BlockedIpListComponent extends PageBaseComponent implements OnInit {

  displayedColumns: string[] = ['order', 'task', 'ip', 'isPrivateBrowsing', 'network', 'blockingOver', 'keyword', 'campaignType', 'matchType', 'page', 'position'];

  dataSource = [];

  currentPageNumber: number;
  pageTotal: number;
  isProcessing: boolean = false;
  totalItems: number;
  pageLimit: number = 20;

  constructor(
    public sessionService: SessionService,
    private _fuseProgressBarService: FuseProgressBarService,
    private _dialogService: DialogService,
    private _reportService: ReportService,
    private _activatedRoute: ActivatedRoute,
    public router: Router,
    private _fuseSplashScreenService: FuseSplashScreenService
  ) {
    super();
  }

  ngOnInit() {
    this._fuseProgressBarService.show();
    const sub = this.sessionService.getAccountId()
      .subscribe((accountId: string) => {
        if (accountId) {
          this.pageTotal = 0;

          const selectedActiveAccount = this.sessionService.getValueOfSelectedActiveAccount();
          if (selectedActiveAccount) {
            this.currentPageNumber = 1;
          }
          else {
            const page = this._activatedRoute.snapshot.queryParamMap.get('page');

            if (page) {
              if (isNaN(Number(page))) {
                this.currentPageNumber = 1;
              }
              else {
                this.currentPageNumber = Number(page);
              }
            }
            else {
              this.currentPageNumber = 1;
            }
          }
          this.router.navigate([], {
            queryParams: {
              page: this.currentPageNumber,
            }
          });

          this.getBlockedIPsListReport(this.currentPageNumber);
        }
      });
    this.subscriptions.push(sub);
  }

  parseIP(ip: string): string {
    if (ip.includes('.*.*'))
      return ip.replace('.*.*', '.0.0') + '/16';
    if (ip.includes('.*'))
      return ip.replace('.*', '.0') + '/24';
    return ip;
  }

  showCampaignListDialog(campaigns: any) {
    this._dialogService._openCampaignDialog(campaigns);
  }

  getBlockedIPsListReport(page: number) {
    this.isProcessing = true;
    this._fuseProgressBarService.show();

    const sub = this._reportService.getBlockedIPsListReport({ page, limit: this.pageLimit })
      .subscribe(res => {

        // const data = { "_id": "27.78.214.152", "campaigns": [{ "campaignId": "954282145", "campaignName": "1. Bo hnh tivi" }, { "campaignId": "933358694", "campaignName": "1. Tivi - chung - khng xc nh" }, { "campaignId": "763908172", "campaignName": "1. Tivi - loi" }, { "campaignId": "764202431", "campaignName": "1. Tivi - thng hiu" }, { "campaignId": "764224443", "campaignName": "1.Tivi - chung" }, { "campaignId": "954320764", "campaignName": "2. Bo hnh t lnh" }, { "campaignId": "764596510", "campaignName": "2. T lnh - Chung" }, { "campaignId": "933808708", "campaignName": "2. T lnh - Chung - khng xc nh" }, { "campaignId": "764944701", "campaignName": "2. T lnh - Thng Hiu" }, { "campaignId": "764988188", "campaignName": "2.T lnh - qun" }, { "campaignId": "956091823", "campaignName": "3. Bo hnh my git" }, { "campaignId": "957120444", "campaignName": "3. My git - chung - khng xc nh" }, { "campaignId": "766431547", "campaignName": "3. My git - thng hiu" }, { "campaignId": "767004414", "campaignName": "3.My git - chung" }, { "campaignId": "767020272", "campaignName": "3.My git - Qun" }, { "campaignId": "2008215084", "campaignName": "5. T lnh - Thng Hiu #2" }, { "campaignId": "1152293962", "campaignName": "8. My Lnh - Chung - khng xc nh" }, { "campaignId": "765839244", "campaignName": "8. My lnh - Thng hiu" }, { "campaignId": "1726042940", "campaignName": "Bao hnh - Top" }, { "campaignId": "767008514", "campaignName": "L vi sng - chung" }, { "campaignId": "766682305", "campaignName": "L vi sng - Qun" }, { "campaignId": "766652857", "campaignName": "L vi sng - Thng hiu" }, { "campaignId": "765894921", "campaignName": "My Lnh - Qun" }, { "campaignId": "1729464644", "campaignName": "Search. Dynamic.All" }, { "campaignId": "780442513", "campaignName": "Search.Bo Hnh - Thng hiu" }, { "campaignId": "1726030931", "campaignName": "Search.Bo Hnh - Thng hiu chay Top" }, { "campaignId": "764777026", "campaignName": "Search.My Lnh - Chung" }], "numberOfCampaigns": 27, "network": null, "isPrivateBrowsing": false, "gclid": "EAIaIQobChMImqWSpNj55AIVzw0rCh0z5AzwEAAYASAAEgIRHvD_BwE", "gclidInfo": { "_id": "5d93a23c698ac43420c6534d", "keywordMatchType": "Khng xc nh", "device": "in thoi", "clickType": "Headline", "keywordId": "19959388920", "keyword": "", "adGroupId": "64817373101", "adGroupName": "All Website", "campaignId": "1729464644", "campaignName": "Search. Dynamic.All", "gclId": "EAIaIQobChMImqWSpNj55AIVzw0rCh0z5AzwEAAYASAAEgIRHvD_BwE", "lopRegionCriteriaId": "9040373", "lopMostSpecificTargetId": "1028581", "lopCityCriteriaId": "1028581", "lopCountryCriteriaId": "2704", "dateOfTakingReport": "2019-10-01T00:00:00.000Z", "__v": 0, "createdAt": "2019-10-01T19:00:13.076Z", "updatedAt": "2019-10-01T19:00:13.076Z" } };
        // this.dataSource.push(data);

        this.dataSource = res.data.ips;

        this.totalItems = res.data.totalItems;
        this.pageTotal = Math.ceil(this.totalItems / this.pageLimit) || 0;

        setTimeout(() => {
          this._fuseProgressBarService.hide();
          this._fuseSplashScreenService.hide();
          this.isProcessing = false;
        }, 500);
      },
        (error: HttpErrorResponse) => {
          this.dataSource = [];
          this.pageTotal = 0;
          this.totalItems = 0;
          this._fuseProgressBarService.hide();
          this._fuseSplashScreenService.hide();
          this._dialogService._openErrorDialog(error.error);
          this.isProcessing = false;
        });
    this.subscriptions.push(sub);
  }

  openRemoveAutoBlockedIPConfirmDialog(ip: string) {
    const openConfirmDialogSub = this._dialogService._openConfirmDialog('B chn IP ny?')
      .subscribe(
        (isAccepted: boolean) => {
          if (isAccepted)
            this.removeAutoBlockedIP(ip);
        });
    this.subscriptions.push(openConfirmDialogSub);
  }

  removeAutoBlockedIP(ip: string) {
    this.isProcessing = true;
    this._fuseProgressBarService.show();
    const sub = this._reportService.removeAutoBlockedIP({ ips: [this.parseIP(ip)] })
      .subscribe(
        (res: ILoginSuccess) => {

          this.getBlockedIPsListReport(this.currentPageNumber);

          setTimeout(() => {
            this._fuseProgressBarService.hide();
            this._dialogService._openSuccessDialog(res);
            this.isProcessing = false;
          }, 0);
        },
        (error: HttpErrorResponse) => {
          this._fuseProgressBarService.hide();

          if (error.status === 404) {
            this._dialogService._openErrorDialog({
              messages: [`${error.error.data.ips[0]} khng nm trong blacklist.`]
            });
          }
          else this._dialogService._openErrorDialog(error.error);

          this.isProcessing = false;
        }
      );
    this.subscriptions.push(sub);
  }

  changePage(event) {
    this.getBlockedIPsListReport(event);
    this.router.navigate([], {
      queryParams: {
        page: event,
      }
    });
  }

}
